<template>
    <div>
        <u-radio size="large" v-model="single">u-radio</u-radio>
        <u-radio size="default" v-model="single">u-radio</u-radio>
        <u-radio size="small" v-model="single">u-radio</u-radio>
        <br><br>
        <u-radio-group v-model="phone">
            <u-radio label="apple" color="red|blue">
                <u-icon type="social-apple"></u-icon>
                <span>Apple</span>
            </u-radio>
            <u-radio label="android">
                <u-icon type="social-android"></u-icon>
                <span>Android</span>
            </u-radio>
            <u-radio label="windows">
                <u-icon type="social-windows"></u-icon>
                <span style="color: red;">Windows</span>
            </u-radio>
        </u-radio-group>
        <u-radio-group v-model="button2" type="button" size="large">
            <u-radio label="北京"></u-radio>
            <u-radio label="上海" disabled></u-radio>
            <u-radio label="深圳"></u-radio>
            <u-radio label="杭州"></u-radio>
        </u-radio-group>
        <u-radio-group v-model="button2" type="button" size="default">
            <u-radio label="北京"></u-radio>
            <u-radio label="上海" disabled></u-radio>
            <u-radio label="深圳"></u-radio>
            <u-radio label="杭州"></u-radio>
        </u-radio-group>
        <u-radio-group v-model="button2" type="button" size="small">
            <u-radio label="北京"></u-radio>
            <u-radio label="上海" disabled></u-radio>
            <u-radio label="深圳"></u-radio>
            <u-radio label="杭州"></u-radio>
        </u-radio-group>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                single: true,
                phone: '',
                button2: '北京',
            };
        },
        methods: {

        }
    };
</script>



